.pill-button {
  display: inline-flex;
  position: relative;
  padding: 0 1.75rem;
  height: 2.75rem;
  text-align: center;
  text-decoration: none;
  align-items: center;
  justify-content: center
}

.pill-button.is-big {
  padding: 0 2.5rem;
  height: 3.5rem
}

html.is-mouse .pill-button:hover .background .left .plain,html.is-mouse .pill-button:hover .background .right .plain {
  transform: translate(0)
}

.pill-button .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%
}

.pill-button .background .left {
  position: absolute;
  top: 0;
  left: -15px;
  right: 50%;
  height: 100%;
  overflow: hidden
}

.pill-button .background .left .plain {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-top-left-radius: 35px;
  border-bottom-left-radius: 35px;
  background: rgba(255,255,255,.047);
  transform: translate(12px);
  transition: transform .2s;
  will-change: transform
}

.pill-button .background .right {
  position: absolute;
  top: 0;
  right: -15px;
  left: 50%;
  height: 100%;
  overflow: hidden
}

.pill-button .background .right .plain {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-top-right-radius: 35px;
  border-bottom-right-radius: 35px;
  background: rgba(255,255,255,.047);
  transform: translate(-12px);
  transition: transform .2s;
  will-change: transform
}

.pill-button .label {
  display: flex;
  align-items: center;
  position: relative
}

.experience {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh
}

.sections {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: hidden;
  pointer-events: none
}

html.is-vertical.is-focused .sections {
  pointer-events: auto
}

.sections.is-scrollable {
  overflow-y: auto
}

.section {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none
}

.section.is-crew {
  --section-color: #a48bff
}

.section.is-privatekey {
  --section-color: #85a5ff
}

.section.is-onscout {
  --section-color: #fff
}

.section.is-isonline {
  --section-color: #f2acf2
}

.section.is-ideasby {
  --section-color: #f9d77f
}

.section.is-takemeto {
  --section-color: #fff
}

.section *::selection {
  background: var(--section-color)
}

.section *::-moz-selection {
  background: var(--section-color)
}

.section .reveal {
  transition: opacity .3s,transform .3s
}

html.is-vertical .section.is-active,.section.is-active .content * {
  pointer-events: auto
}

.section.is-active .reveal {
  opacity: 1;
  transform: translate(0);
  transition-timing-function: ease-out,ease-out
}

.section.is-active .reveal.delay-in-0 {
  transition-delay: .3s
}

.section.is-active .reveal.delay-in-1 {
  transition-delay: .35s
}

.section.is-active .reveal.delay-in-2 {
  transition-delay: .4s
}

.section.is-active .reveal.delay-in-3 {
  transition-delay: .45s
}

.section.is-active .reveal.delay-in-4 {
  transition-delay: .5s
}

.section.is-active .reveal.delay-in-5 {
  transition-delay: .55s
}

.section.is-active .reveal.delay-in-6 {
  transition-delay: .6s
}

.section.is-active .reveal.delay-in-7 {
  transition-delay: .65s
}

.section.is-active .reveal.delay-in-8 {
  transition-delay: .7s
}

.section.is-active .reveal.delay-in-9 {
  transition-delay: .75s
}

.section.is-active .reveal.delay-in-10 {
  transition-delay: .8s
}

.section.is-active .focus-point {
  transform: scale(0);
  transition-delay: 0s
}

.section:not(.is-active) .reveal {
  opacity: 0;
  transform: translate(60px);
  transition-timing-function: ease-in,ease-in
}

.section:not(.is-active) .reveal.delay-out-0 {
  transition-delay: 0s
}

.section:not(.is-active) .reveal.delay-out-1 {
  transition-delay: .05s
}

.section:not(.is-active) .reveal.delay-out-2 {
  transition-delay: .1s
}

.section:not(.is-active) .reveal.delay-out-3 {
  transition-delay: .15s
}

.section:not(.is-active) .reveal.delay-out-4 {
  transition-delay: .2s
}

.section:not(.is-active) .reveal.delay-out-5 {
  transition-delay: .25s
}

.section:not(.is-active) .reveal.delay-out-6 {
  transition-delay: .3s
}

.section:not(.is-active) .reveal.delay-out-7 {
  transition-delay: .35s
}

.section:not(.is-active) .reveal.delay-out-8 {
  transition-delay: .4s
}

.section:not(.is-active) .reveal.delay-out-9 {
  transition-delay: .45s
}

.section:not(.is-active) .reveal.delay-out-10 {
  transition-delay: .5s
}

