}



.section.is-content .main-link .background .left .plain,.section.is-content .main-link .background .right .plain {
  background: var(--section-color)
}

.section .focus-point {
  position: absolute;
  top: 50%;
  left: -150px;
  width: 150px;
  height: 150px;
  margin-top: -50px;
  margin-left: -50px;
  cursor: pointer;
  pointer-events: auto;
  transform: scale(1);
  transition: transform .3s cubic-bezier(.5,0,.5,1);
  transition-delay: .3s
}

.section .focus-point:hover .background {
  transform: scale(1);
  transition-delay: 0s;
  transition-timing-function: cubic-bezier(.2,0,.5,1.45)
}

.section .focus-point:hover .plus-icon {
  opacity: 0;
  transition-delay: 0s
}

.section .focus-point:hover .label {
  opacity: 1;
  transition-delay: .2s
}

.section .focus-point .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: var(--section-color);
  transform: scale(.53);
  transition: transform .3s cubic-bezier(.6,-.45,.75,.9);
  transition-delay: 0s;
  will-change: transform
}

.section .focus-point .plus-icon {
  position: absolute;
  top: calc(50% - 15px);
  left: calc(50% - 15px);
  width: 30px;
  height: 30px;
  opacity: 1;
  transition: opacity .3s;
  transition-delay: .2s;
  will-change: opacity
}

.section .focus-point .plus-icon:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: calc(50% - 2px);
  width: 4px;
  height: 100%;
  background: #000
}

.section .focus-point .plus-icon:after {
  content: "";
  display: block;
  position: absolute;
  top: calc(50% - 2px);
  left: 0;
  width: 100%;
  height: 4px;
  background: #000
}

.section .focus-point .label {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 20px;
  color: #000;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity .3s;
  transition-delay: 0s;
  will-change: opacity
}

.scroll-down-container {
  display: flex;
  position: fixed;
  left: 0;
  width: 100%;
  bottom: 6%;
  pointer-events: none;
  align-items: center;
  justify-content: center
}

.scroll-down {
  pointer-events: none;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity .6s 0s ease-in,transform .6s 0s ease-in;
  will-change: opacity,transform
}

.scroll-down.is-visible {
  pointer-events: auto;
  opacity: 1;
  transform: translateY(0);
