html {
  cursor: none
}

.cursors {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none
}

.cursor {
  position: absolute;
  transform: translate(-20px,-20px);
  will-change: transform;
  pointer-events: none
}

.cursor .small {
  display: none;
  position: absolute;
  top: -5px;
  left: -5px;
  width: 10px;
  height: 10px;
  border: 6px solid #fff;
  border-radius: 50%;
  transition-property: top,left,width,height,border-width;
  transition-duration: .2s
}

html.is-mouse .cursor .small {
  display: block
}

html.is-hovering-link .cursor .small {
  top: -12px;
  left: -12px;
  width: 24px;
  height: 24px;
  border-width: 4px
}

html.is-can-navigate.is-hovering-box .cursor .small {
  transform: scale(0)
}

.cursor .big {
  transform: scale(0);
  transition: transform .2s ease-in
}

html.is-can-navigate.is-vertical:not(.is-focused-once) .cursor .big {
  transform: scale(1)
}

html.is-can-navigate.is-vertical.is-focused .cursor .big {
  transform: scale(1)
}

html.is-can-navigate.is-hovering-box .cursor .big {
  transform: scale(1);
  transition-timing-function: cubic-bezier(.6,2.1,.8,1);
  transition-duration: .3s
}

.cursor .big .background {
  position: absolute;
  top: -35px;
  left: -35px;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: #a48bff;
  transition: background-color 0s .3s
}

html.is-section-crew .cursor .big .background {
  background-color: #a48bff
}

html.is-section-privatekey .cursor .big .background {
  background-color: #85a5ff
}

html.is-section-onscout .cursor .big .background {
  background-color: #fff
}

html.is-section-isonline .cursor .big .background {
  background-color: #f2acf2
}

html.is-section-ideasby .cursor .big .background {
  background-color: #f9d77f
}

html.is-section-takemeto .cursor .big .background {
  background-color: #fff
}

.cursor .big .plus-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transition: transform .5s;
  will-change: transform
}

html.is-vertical .cursor .big .plus-icon {
  transform: scale(0)
}

html.is-vertical.is-focused .cursor .big .plus-icon {
  transform: scale(1)
}

.cursor .big .plus-icon .plus-icon-inner {
  position: absolute;
  top: -15px;
  left: -15px;
  width: 30px;
  height: 30px;
  transition: transform .5s;
  will-change: transform
}

html.is-focused .cursor .big .plus-icon .plus-icon-inner {
  transform: rotate(225deg)
}

.cursor .big .plus-icon .plus-icon-inner:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: calc(50% - 2px);
  width: 4px;
  height: 100%;
  background: #000
}

.cursor .big .plus-icon .plus-icon-inner:after {
