.box-area-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none
}

.box-area {
  position: absolute;
  top: calc(50% - 40vh);
  left: calc(50% - 30vh);
  height: 80vh;
  width: 60vh;
  pointer-events: auto
}

.box-area.is-pending {
  display: none
}

html.is-vertical .box-area {
  top: calc(50% - 50vw);
  left: calc(50% - 40vw);
  width: 80vw;
  height: 100vw
}

html.is-focused .box-area {
  top: calc(50% - 40vh);
  left: 0;
  height: 80vh;
  width: 50%
}

html.is-vertical.is-focused .box-area {
  top: 0;
  right: 0;
  left: auto;
  width: 30%;
  height: 16%;
  margin-top: 0;
  margin-left: 0
}

.main-intro {
